<template>
    <a-form :form="form" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }" @submit="handleSubmit">
        <a-form-item :label="locale.tid">
            <a-select
                    :default-value="`${callingCode[0].show} ${callingCode[0][lan]}`"
                    style="width: 150px"
                    @change="countryChange"
            >
                <a-select-option v-for="item in callingCode" :key="item.en" :id="item.show">
                    {{item.show}} {{item[lan]}}
                </a-select-option>
            </a-select>
            <a-input
                    v-decorator="['tid', { rules: [{ required: true, message: `${locale.error}${locale.tid}` }] }]"
            />
        </a-form-item>
        <a-form-item :label="locale.tn">
            <a-input
                    v-decorator="['tn', { rules: [{ required: true, message: `${locale.error}${locale.tn}` }] }]"
            />
        </a-form-item>
        <a-form-item :label="locale.vid">
            <a-row :gutter="8">
                <a-col :span="19">
                    <a-input
                            v-decorator="['vid', { rules: [{ required: true, message: `${locale.error}${locale.vid}`}] }]"
                    />
                </a-col>
                <a-col :span="3">
                    <a-button @click="goRegister">{{ locale.register }}</a-button>
                </a-col>
            </a-row>
        </a-form-item>
        <a-form-item :label="locale.bid">
            <a-input
                    v-decorator="['bid', { rules: [{ required: true, message: `${locale.error}${locale.bid}`}] }]"
            />
        </a-form-item>
        <a-form-item :label="locale.wx">
            <a-input
                    v-decorator="['wx', { rules: [{ required: true, message: `${locale.error}${locale.wx}`}] }]"
            />
        </a-form-item>
        <a-form-item :label="locale.wxn">
            <a-input
                    v-decorator="['wxn', { rules: [{ required: true, message: `${locale.error}${locale.wxn}` }] }]"
            />
        </a-form-item>
        <a-form-item :label="locale.email">
            <a-input
                    v-decorator="['email', { rules: [{ required: true, message: `${locale.error}${locale.email}` }] }]"
            />
        </a-form-item>
        <a-form-item :label="locale.phone">
            <a-input
                    v-decorator="['phone', { rules: [{ required: true, message: `${locale.error}${locale.phone}` }] }]"
            />
        </a-form-item>
        <a-form-item :label="locale.tc">
            <a-input
                    v-decorator="['tc', { rules: [{ required: true, message: `${locale.error}${locale.tc}`}] }]"
            />
        </a-form-item>
        <a-form-item :label="locale.tv">
            <a-input
                    v-decorator="['tv', { rules: [{ required: true, message: `${locale.error}${locale.tv}` }] }]"
            />
        </a-form-item>
        <a-form-item :label="locale.address">
            <a-row :gutter="8">
                <a-col :span="19">
                    <div style="margin-top:-4px;padding: 4px 11px;text-align:center;border-radius: 4px;font-weight:600;border: 1px solid #d9d9d9; text-indent:10px">
                        0x9FD03096674E0b9B0cBF37Ca7209c5F0FD3Dfc5F
                    </div>
                </a-col>
                <a-col :span="3">
                    <a-button @click="copy" style="margin-top: 6px">{{ locale.copy }}</a-button>
                </a-col>
            </a-row>
        </a-form-item>
        <template v-for="i in 1">
            <h2 style="text-align: center">{{ locale.addressWrite }}</h2>
            <a-form-item :label="locale.hash">
                <a-input
                        v-decorator="[`hash${i}`, { rules: [{ required: true, message: `${locale.error}${locale.hash}` }] }]"
                />
            </a-form-item>
            <a-form-item :label="locale.hc">
                <a-input
                        v-decorator="[`hc${i}`, { rules: [{ required: true, message: `${locale.error}${locale.hc}` }] }]"
                />
            </a-form-item>
        </template>
        <a-form-item label="" v-bind="tailFormItemLayout" ref="agree">
            <p class="ant-checkbox-wrapper" style="line-height: 16px;width: 300px;margin: 0 auto;cursor:unset"
               v-html="locale.agDesc"></p>
            </br>
            <a-checkbox :disabled="okDisabled"
                        v-decorator="['agreement', { valuePropName: 'checked', required: true }]">
                {{ locale.agree }}
            </a-checkbox>
        </a-form-item>
        <a-form-item :wrapper-col="{ span: 12, offset: 5 }">
            <a-button type="primary" html-type="submit">
                {{ locale.submit }}
            </a-button>
        </a-form-item>
        <a-modal
                v-model="visible"
                :title="locale.xieyi"
                class="tip"
                :okText="locale.known"
                :okButtonProps="{props:{disabled:okDisabled}}"
                @ok="hideModal">
            <div style="overflow: auto; height: 500px" ref="planScroll" v-html="getXieyi()"></div>
        </a-modal>
      <div class="timeout-container">
        <div class="timeout-box">
          <p>{{$t('applyForm.timeOut')}}</p>
        </div>
      </div>
    </a-form>
</template>

<script>
    import {callingCode} from '../../js/countryCodes';
    import axios from 'axios';

    function u(e) {
        e.clipboardData.setData("text/plain", '0x9FD03096674E0b9B0cBF37Ca7209c5F0FD3Dfc5F');
        e.preventDefault();
        this.$message.success(this.locale.copySuccess);
    }

    export default {
        name: "index",
        created() {

        },
        mounted() {
            this.$refs.agree.$el.addEventListener('click', (e) => {
                const target = e.target;
                if (target.id === 'plan') {
                    this.visible = true;
                }
            }, false);
        },
        watch: {
            visible(val) {
                if (val) {
                    this.$nextTick(() => {
                        this.$refs.planScroll.addEventListener('scroll', this.scrollCheck, false);
                    })
                } else {
                    this.$nextTick(() => {
                        this.$refs.planScroll.removeEventListener('scroll', this.scrollCheck);
                    });
                }
            }
        },
        computed: {
            locale() {
                return this.$t('applyForm');
            },
            lan() {
                return this.$i18n.locale.replace('lang-', '');
            }
        },
        data() {
            return {
                formLayout: 'horizontal',
                callingCode,
                visible: false,
                okDisabled: true,
                country: callingCode[0].show,
                form: this.$form.createForm(this, {name: 'coordinated'}),
                formItemLayout: {
                    labelCol: {
                        xs: {span: 24},
                        sm: {span: 5},
                    },
                    wrapperCol: {
                        xs: {span: 24},
                        sm: {span: 10},
                    },
                },
                tailFormItemLayout: {
                    wrapperCol: {
                        xs: {
                            span: 24,
                            offset: 0,
                        },
                        sm: {
                            span: 16,
                            offset: 8,
                        },
                    },
                },
            };
        },
        methods: {
            getXieyi: function() {
                if (lang === 'lang-zh') {
                    return `
                        <p>关于Tweebaa 推易吧官方公告(公告字第 20200911 号)关怀补贴计划内容，
                            投资者与推易吧双方高度认可并对所有内容达成共识。现双方就共识内容进入 落地执行阶段，本着“合作共赢，合法合规”的原则签定本协议。</p>
                        <h2>一、合作内容</h2>
                        <p>1. 详情参考Tweebaa推易吧官方公告(公告字第20200911号)关怀
                            补贴计划。</p>
                        <h2>二、协议期限</h2>
                        <p>1. 本协议有限期自2020年9月25日24时至12月24日24时。</p>
                        <h2>三、双方权利义务</h2>
                        <p>1. 投资者有责任和义务遵照推易吧官方公告(公告字第20200911号) 关怀补贴计划内容里的时间表和流程配合完成完整的资料信息提供。</p>
                        <p>2. 推易吧有责任和义务在共识的协议期限内完全落实Tweebaa推易吧
                            官方公告(公告字第 20200911 号)里对甲方承诺的关怀补贴计划或
                            特殊关怀补贴计划内容;</p>
                        <p>3. 投资者在签订该协议后便视为完全自愿认可推易吧官方公告(公告字
                            第 20200911 号)关怀补贴计划内容，不得再对共识内容提出质疑， 也不得有任何其他违背该协议的行为。</p>
                        <h2>四、其他约定</h2>
                        <p>1. 甲乙双方应对本协议承担保密责任，任何一方不得将本协议内容以任
                            何形式传播。</p>
                        <p>2. 本协议自签订之日起生效，未尽事宜，双方协商解决。</p>
                        <h2>五.免责声明</h2>
                        <p>推易吧有权根据此协议执行关怀补贴计划，前述决定作出即生效，您同意
                            推易吧对此免责。</p>
                        <h2>六.联系我们</h2>
                        <p>如对本协议或相关事宜有任何问题，您可随时通过联系推易吧在线客服或 发送邮件至【love@tweebaa.com】与我们联系。</p>
                    `
                } else {
                    return `
                        <p>Regarding Tweebaa Official Announcement (Announcement No. 20200913) Instructions on subsidy for Investor Interests, Tweebaa and investors highly agree and reach consensus on all the contents. The agreement now entered the implementation stage, and sign this agreement based on the principle of win-win cooperation and legal compliance.</p>
                        <h2>Agreement</h2>
                        <p>Please fully read and understand Tweebaa Official Announcement (Announcement No. 20200913) Instructions on subsidy Program. </p>
                        <h2>Agreement period</h2>
                        <p>This agreement has a limited period from 24:00 on September 25 to 24:00 on December 24, 2020, GMT+8</p>
                        <h2>The rights and obligations</h2>
                        <p>Investors have the responsibility and obligation to follow the timetable and process in the content of Tweebaa Official Announcement (Announcement No. 20200913) Instructions on Subsidy Program to complete the provision of information completion. </p>
                        <p>Tweebaa has the responsibility and obligation to fully implement the Subsidy Program or Special Subsidy Program promised to Party A in Tweebaa Official Announcement (Announcement No. 20200913) within the agreed agreement period;</p>
                        <p>After signing the agreement, investors are deemed to have fully voluntarily approved the contents of the official announcement of Tweebaa Official Announcement (Announcement No. 20200913) Subsidy Program, and shall no longer question the content of the consensus, nor shall there be any other violations of the agreement.</p>
                        <h2>Other Agreement</h2>
                        <p>Both parties shall bear the responsibility of confidentiality of this agreement, and neither party shall disseminate the contents of this agreement in any form.</p>
                        <p>This agreement will take effect from the date of signing, and the unfinished matters shall be resolved through consultation between the two parties.</p>
                        <h2>Disclaimer</h2>
                        <p>Tweebaa has the right to implement the Subsidy Program according to this agreement, and the aforementioned decision will take effect immediately, and you agree that A is exempted from liability.</p>
                        <h2>Contact Us</h2>
                        <p>The official contact email for this event: love@tweebaa.com</p>

                    `
                }
            },
            copy() {
                document.addEventListener('copy', u.bind(this));
                document.execCommand('copy');
                document.removeEventListener('copy', u);
            },
            goRegister() {
                window.open('https://virgox.com/login/register?code=gwajva9w');
            },
            scrollCheck() {
                setTimeout(() => {
                    if (this.$refs.planScroll.scrollHeight - this.$refs.planScroll.scrollTop, this.$refs.planScroll.clientHeight) {
                        this.okDisabled = false;
                    }
                }, 50);
            },
            showPlan() {
                this.visible = true;
            },
            hideModal() {
                this.visible = false;
            },
            countryChange(value, opt) {
                this.country = opt.data.attrs.id;
            },
            handleSubmit(e) {
                e.preventDefault();
                return;
                this.form.validateFields((err, values) => {
                    if (!err) {
                        if (!/^\d+$/.test(values['tid'])) {
                            const o = {};
                            o['tid'] = {value: values['tid'], errors: [{message: this.locale.noError}]}
                            this.form.setFields(o);
                            return;
                        }
                        if (!/\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/.test(values['email'])) {
                            const o = {};
                            o['email'] = {value: values['email'], errors: [{message: this.locale.emailError}]}
                            this.form.setFields(o);
                            return;
                        }
                        if (!values['agreement']) {
                            const o = {};
                            o['agreement'] = {
                                value: values['agreement'],
                                errors: [{message: this.locale.agreementError}]
                            }
                            this.form.setFields(o);
                            return;
                        }
                        const data = {
                            "countryCallingCode": this.country.replace('+', ''),
                            "phoneNumber": values['tid'],
                            "nickname": values['tn'],
                            "virgoxId": values['vid'],
                            "bitmartId": values['bid'],
                            "wechat": values['wx'],
                            "wechatNickname": values['wxn'],
                            "useEmail": values['email'],
                            "usePhone": values['phone'],
                            "amount": values['tc'],
                            "tivAmount": values['tv'],
                            "details": []
                        };
                        for (let i = 0, len = 6; i < len; i++) {
                            if (values[`hash${i}`] && values[`hc${i}`]) {
                                data.details.push({
                                    "hash": values[`hash${i}`],
                                    "amount": values[`hc${i}`]
                                })
                            }
                        }
                        if (data.details.length === 0) {
                            this.$message.error(this.locale.hashError);
                        }
                        axios({
                            method: 'post',
                            url: 'https://apply.tweebaa.cn/api/v1/pledge',
                            headers: {'content-type': 'application/json'},
                            data,
                        }).then((data) => {
                            if (!Number(data.code)) {
                                this.$message.success(this.locale.success);
                            } else {
                                this.$message.error(this.locale.subError);
                            }
                        });
                    }
                });
            },
        },
    }
</script>

<style>
    .ant-form {
        padding: 2rem 1rem !important;
    }

    .ant-form .ant-btn {
        display: block;
    }

    .ant-form .ant-col {
        word-break: break-all;
    }
    .ant-form .ant-col-19 {
        line-height: 1.6 !important;
    }

    .tip .ant-btn {
        display: none !important;
    }

    .tip .ant-btn-primary {
        display: inline-block !important;
    }

    .tip .ant-modal-close {
        display: none;
    }
    .ant-modal h2 {
        margin: 1rem 0;
    }
    .timeout-container{
      width: 100%;
      height: 100%;
      position: fixed;
      top: 0;
      left: 0;
      z-index: 100;
      background-color: rgba(0,0,0,0.45);
    }
    .timeout-box p{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background-color: #fff;
        background-clip: padding-box;
        color: rgba(0,0,0,.85);
        font-weight: 500;
        font-size: 16px;
        border: 0;
        border-radius: 4px;
        box-shadow: 0 4px 12px rgba(0,0,0,.15);
        pointer-events: auto;
        padding: 50px;
    }

</style>
