.footer-aside {
  margin-top: 80px;
  bottom: 0;
  left: 0;
  width: 100%;
}
.hero {
  width: 1000px;
  margin: 0 auto;
}
.hero-content {
  margin-bottom: 60px;
  height: 100%;
}
.hero-content .left {
  display: flex;
  flex-direction: column;
  width: 80%;
  height: 80%;
  justify-content: center;
}
.hero-content .right {
  width: 60%;
  height: 85%;
  -webkit-background-size: contain;
  -moz-background-size: contain;
  -o-background-size: contain;
  background-size: contain;
  position: absolute;
  top: 100px;
  right: 0;
  z-index: -2;
}
.title {
  font-family: "Noto Sans SC";
  font-weight: 400;
  color: #20242B;
  font-size: 3.125rem;
  line-height: 48px;
}
.title .strong {
  font-size: 32px;
}
.title span {
  font-weight: 300;
  color: #1652F0;
  font-size: 1.25rem;
  line-height: 1rem;
}
.download {
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  margin-top: 2rem;
  width: 100%;
}
.download-btn:first-child {
  margin-right: 1rem;
}
.module-apply {
  width: 1000px;
  margin: 0 auto;
}
.module-apply input[type="text"] {
  -webkit-appearance: none;
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  font-size: inherit;
  height: 40px;
  line-height: 40px;
  outline: none;
  padding: 0 15px;
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  width: 320px;
}
.module-apply input[type="text"]:focus {
  outline: none;
  border-color: #409eff;
}
.module-apply .el-radio {
  color: #606266;
  font-weight: 500;
  line-height: 40px;
  position: relative;
  cursor: pointer;
  display: inline-block;
  white-space: nowrap;
  outline: none;
  font-size: 14px;
  margin-right: 30px;
}
.module-apply .el-button {
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #fff;
  border: 1px solid #dcdfe6;
  color: #606266;
  -webkit-appearance: none;
  text-align: center;
  box-sizing: border-box;
  outline: none;
  margin: 0;
  transition: 0.1s;
  font-weight: 500;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  padding: 12px 20px;
  font-size: 14px;
  border-radius: 4px;
}
.module-apply .el-button-primary {
  color: #fff;
  background-color: #1652F0;
  border-color: #1652F0;
}
.module-apply .el-button + .el-button {
  margin-left: 10px;
}
.module-apply .form-title {
  margin-bottom: 16px;
}
.module-apply .m-form {
  padding: 24px;
  border: 1px solid #ebebeb;
  width: 100%;
  font-size: 14px;
  background-color: #fff;
  border-radius: 4px;
}
.module-apply .m-form .ui-tip {
  margin-top: 2px;
  min-height: 16px;
  line-height: 16px;
  color: #333;
  font-size: 12px;
}
.module-apply .m-form .ui-tip .error {
  display: block;
  color: #ff5353;
}
.module-apply .m-form .col-two {
  display: flex;
}
.module-apply .m-form .col-two .col-row {
  flex: 1;
}
.module-apply .m-form .col-row {
  display: flex;
  margin-bottom: 16px;
}
.module-apply .m-form .col-row .col-label {
  width: 100px;
  padding-top: 10px;
  color: #677382;
  text-align: right;
  margin-right: 32px;
}
.module-apply .m-form .col-row .col-input,
.module-apply .m-form .col-row .col-text {
  flex: 1;
}
.module-apply .m-form .col-row .col-input-full .input,
.module-apply .m-form .col-row .col-text-full .input {
  width: 796px;
}
.module-apply .m-form .col-row-do {
  margin-top: 32px;
}
