
@color-blue: #1652F0;
@color-red: #ff7f00;
@color-green: #4fbb92;

@night-text: #333;
@text: #333;
@container-width: 1000px;

.footer-aside {
    margin-top: 80px;
    bottom: 0;
    left: 0;
    width: 100%;
}

.hero {
    width: @container-width;
    margin: 0 auto;
}

.hero-content {
    margin-bottom: 60px;
    height: 100%;
    .left {
        display: flex;
        flex-direction: column;
        width: 80%;
        height: 80%;
        justify-content: center;
    }
    .right {
        width: 60%;
        height: 85%;
        //background: url(../../images/about/hero.png) no-repeat right center;
        -webkit-background-size: contain;
        -moz-background-size: contain;
        -o-background-size: contain;
        background-size: contain;
        position: absolute;
        top: 100px;
        right: 0;
        z-index: -2;
    }
}
.title {
    font-family: "Noto Sans SC";
    font-weight: 400;
    color: #20242B;
    font-size: 3.125rem;
    line-height: 48px;
    .strong {
        font-size: 32px;
    }
    span {
        font-weight: 300;
        color: #1652F0;
        font-size: 1.25rem;
        line-height: 1rem;
    }
}
.download {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-top: 2rem;
    width: 100%;
}
.download-btn {
    &:first-child {
        margin-right: 1rem;
    }
}

.module-apply {

    width: @container-width;
    margin: 0 auto;

    @height: 40px;

    input[type="text"] {
        -webkit-appearance: none;
        background-color: #fff;
        background-image: none;
        border-radius: 4px;
        border: 1px solid #dcdfe6;
        box-sizing: border-box;
        color: #606266;
        display: inline-block;
        font-size: inherit;
        height: @height;
        line-height: @height;
        outline: none;
        padding: 0 15px;
        transition: border-color .2s cubic-bezier(.645,.045,.355,1);
        width: 320px;
        &:focus {
            outline: none;
            border-color: #409eff;
        }
    }

    .el-radio {
        color: #606266;
        font-weight: 500;
        line-height: @height;
        position: relative;
        cursor: pointer;
        display: inline-block;
        white-space: nowrap;
        outline: none;
        font-size: 14px;
        margin-right: 30px;

        .radio {

        }
        .label {

        }
    }

    .el-button {
        display: inline-block;
        line-height: 1;
        white-space: nowrap;
        cursor: pointer;
        background: #fff;
        border: 1px solid #dcdfe6;
        color: #606266;
        -webkit-appearance: none;
        text-align: center;
        box-sizing: border-box;
        outline: none;
        margin: 0;
        transition: .1s;
        font-weight: 500;
        -moz-user-select: none;
        -webkit-user-select: none;
        -ms-user-select: none;
        padding: 12px 20px;
        font-size: 14px;
        border-radius: 4px;

        &-primary {
            color: #fff;
            background-color: #1652F0;
            border-color: #1652F0;
        }
    }
    .el-button+.el-button {
        margin-left: 10px;
    }

    .form-title {
        margin-bottom: 16px;
    }

    .m-form {
        padding: 24px;
        border: 1px solid #ebebeb;
        width: 100%;
        @input-height: 32px;
        font-size: 14px;
        background-color: #fff;
        border-radius: 4px;
        .ui-tip {
            margin-top: 2px;
            min-height: 16px;
            line-height: 16px;
            color: @night-text;
            font-size: 12px;
            .error {
                display: block;
                color: rgb(255, 83, 83);
            }
        }
        .col-two {
            display: flex;
            .col-row {
                flex: 1;
            }
        }
        .col-row {
            display: flex;
            margin-bottom: 16px;
            .col-label {
                width: 100px;
                padding-top: 10px;
                color: #677382;
                text-align: right;
                margin-right: 32px;
            }
            .col-input, .col-text {
                flex: 1;
                .input {

                }
                &-full {
                    .input {
                        width: 796px;
                    }
                }
            }
            &-do {
                margin-top: 32px;
            }
        }
    }


}
